import { EditFreightComponent } from '../app/freights/edit-freight/edit.freight.component';
import { DeleteFreightComponent } from '../app/freights/delete-freight/delete.freight.component';
import { FreightsComponent } from './../app/freights/freights.component';
import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { CreateFreightComponent } from '../app/freights/create-freight/create.freight.component';
import { AddFreightsToContainerComponent } from 'src/app/freights/add-freights-to-container/add.freights.to.container.component';

const ROUTS: Routes = [
    {
        path: '', component: FreightsComponent,
        children: [
            {
                path: "delete/:targetId",
                component: DeleteFreightComponent,
            },
            {
                path: "addToContainer",
                component: AddFreightsToContainerComponent,
            }
        ]
    },
    { path: 'create', component: CreateFreightComponent },
    { path: 'edit/:targetId', component: EditFreightComponent  },
];

@NgModule({
    imports: [RouterModule.forChild(ROUTS)],
    exports: [RouterModule]
})
export class FreightsRoutingModule {

}

