import { ResponseTypeEnum } from 'src/enums/response.type.enum';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { SharedComponents } from 'src/app/shared.components';
import { AlertService } from 'src/services/alert.service';
import { CurrentUserService } from 'src/services/authentcation/current.user.service';
import { HttpService } from 'src/services/http/http.service';
import { FreightService } from 'src/services/freight.service';
import { ITransaction } from 'src/interfaces/i.transaction.interface';

@Component({
  selector: 'app-add-freights-to-container',
  templateUrl: './add.freights.to.container.component.html',
  styles: [
  ]
})
export class AddFreightsToContainerComponent extends SharedComponents implements OnInit {
  constructor(public u: CurrentUserService, public freightService: FreightService, public http: HttpService,
    private activateRouter: ActivatedRoute) { super() }

  ngOnInit(): void {

    if (!this.freightService.freightsSelected.length) {
      AlertService.showResponseMessage({
        responseType: ResponseTypeEnum.error,
        message: this.u.r.youMustSelectFreightsFirst
      } as any);
      this.freightService.closeModel();
    }
    this.freightService.getItems("containers");

    this.freightService.form = new FormGroup({
      containerId: new FormControl(null, Validators.required)
    });
  }

  /**
   * Delete Item
   */
  onSubmit() {
    if (this.freightService.form.invalid) {
      this.freightService.formSubmitError = true;
      return;
    }
    this.freightService.isRequestingToServer = true;
    this.allSubscriptions.push(
      this.freightService.http.moveFreightsToContainer<ITransaction>(this.freightService.form.controls.containerId.value, this.freightService.freightsSelected.map(f => f._id))
        .subscribe(res => {
          AlertService.showResponseMessage(res);
          this.freightService.isRequestingToServer = false;
          if (res.isSuccess) {
            let containerName = this.freightService.containers.find(c => c._id == this.freightService.form.controls.containerId.value).name;
            this.freightService.freightsSelected.forEach(fe => {
              fe.IsSelected=false;
              fe.containerName = containerName
            });
            this.freightService.freightsSelected=[];
            //Back To Component
            this.freightService.closeModel();
          }
        }, error => {
          this.freightService.isRequestingToServer = false;
          AlertService.canRequestError(error);
        }));
  }


}//End Class
